/* 연락처 */
.contact {
    margin: 0;
    min-width: 450px;
    min-height: 700px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .mbox {
    margin-top: 20px;
    border: 1px solid rgb(95, 25, 25);
    border-radius: 3px;
  }
  .bline {
    border-bottom: 1px solid rgb(95, 25, 25);
  }
  .sbox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 250px;
    padding: 20px;
  }
  

  .icons {
    width: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }