/* 갤러리 */
.gallery {
    margin: 0;
    min-width: 450px;
    min-height: 700px;
    background-color: #fdf7f7;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: rgb(95, 25, 25);
  }
  
  .gallery-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5px;
    max-width: 350px;
    margin: 30px auto;
  }
  
  .gallery-item {
    overflow: hidden;
    border-radius: 3px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }
  
  .photo {
    width: 100%;
    height: auto;
    display: block;
  }
  
  .flower {
    width: 150px;
  }
  
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
  }
  
  .modal .photo {
    max-width: 100px;
    max-height: 100px;
    border-radius: 8px;
  }