.inviation {
    margin: 0;
    min-width: 450px;
    min-height: 650px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .flower {
    width: 150px;
  }
  
  .merry {
    font-size: large;
    margin-bottom: 20px;
  }
  
  .content div {
    margin-bottom: 10px;
  }
  
  .content {
    margin-bottom: 20px;
  }
  
  .parent {
    margin-top: 15px;
    display: flex;
    align-items: center;
  }
  
  .parent div {
    margin-right: 10px;
  }
  
  .person {
    font-weight: bold;
  }
  
  .space5 div {
    margin: 5px;
  }